/* eslint eqeqeq: "off", camelcase: "off" */

// 
const login_with_certification = {
  options: {
    effect: 'scale',
    cssClass: 'popup-general',
    buttons: [
      {text: ''},
      {text: ''}
    ]
  },
  template: `
    <div class="desc no-title"></div>
  `
}

// 
const invalid_certification = {
  options: {
    effect: 'scale',
    cssClass: 'popup-general',
    buttons: [
      {text: ''}
    ]
  },
  template: `
    <div class="title"></div>
    <div class="desc">
      <div><br></div>
    </div>
  `
}

const get_code_fail = {
  options: {
    effect: 'scale',
    cssClass: 'popup-general',
    title: '',
    buttons: [
      {text: ''},
      {text: ''}
    ]
  },
  template: `
    <div class="title"></div>
    <div class="desc"></div>
  `
}

// 
const popup_choose_verify_code_type = {
  options: {
    effect: 'scale',
    cssClass: 'popup-general',
    buttons: [
      { text: '' },
      { text: '' }
    ],
    showClose: true
  },
  template: `
    <div class="title"></div>
    <div class="desc">
      
    </div>
  `
}

module.exports = {
  login_with_certification,
  invalid_certification,
  get_code_fail,
  popup_choose_verify_code_type
}



