/* eslint eqeqeq: "off", camelcase: "off" */
require('./popups.scss')

const popup_important_001 = {
  options: {
    effect: 'scale',
    cssClass: 'popup-general',
    buttons: [
      {text: ''},
      {text: ''}
    ]
  },

  template: (mobile) => `
    <div class="title"></div>
    <div class="desc">
      , , :
    </div>
    <h4 style="position: relative; top: 3px; color: #484746; text-align: center; font-size: 20px; font-weight: normal; margin-bottom:0; line-height: 38px;">${_.reduce(mobile, (s, v, i) => { return s + v + ((i + 2) % 4 == 0 ? ' ' : '') })}</h4>
  `
}

const popup_important_002 = {
  options: {
    effect: 'scale',
    cssClass: 'popup-general',
    buttons: [
      {text: ''},
      {text: ''}
    ]
  },

  template: `
    <div class="title"></div>
    <div class="desc">
      , , 
    </div>
  `
}

const popup_important_003 = {
  options: {
    effect: 'scale',
    cssClass: 'popup-general',
    buttons: [
      {text: ''}
    ]
  },

  template: `
    <div class="title"></div>
    <div class="desc">
      , , 
    </div>
  `
}

const popup_important_004 = {
  options: {
    effect: 'scale',
    cssClass: 'popup-general',
    buttons: [
      {text: ''}
    ]
  },

  template: `
    <div class="title"></div>
    <div class="desc">
      , , 
    </div>
  `
}

// 
const popup_lottery_win = {
  template: (win_info) => {
    let {lottery_id, reward, bets} = win_info

    return `
      <img src="//qianka.b0.upaiyun.com/images/6dc6a001c2292da7bc8ea72bb65e3eeb.png" alt="">
      <div class="win-info">
        <div class="win-money">
          +  <span class="font-big">${reward}</span> 
        </div>
        <div class="win-desc">
          ${lottery_id} ${bets} 
        </div>
      </div>
    `
  }
}

// 
const popup_national_event = {
  options: {
    effect: 'scale',
    cssClass: 'popup-national-event',
    buttons: [
      {text: ''}
    ],
    showClose: true
  },

  template: `
    <div class="title"></div>
    <div class="time"><span>2017.9.30-10.8</span>9)</div>
    <div class="desc">
      1. iOS110
    </div>
    <div class="desc2">2. </div>
  `
}

module.exports = {
  popup_important_001,
  popup_important_002,
  popup_important_003,
  popup_important_004,
  popup_lottery_win,
  popup_national_event
}



