axios.defaults.baseURL = '/'
axios.defaults.headers.post['Content-Type'] = 'application/x-www-form-urlencoded'

let api = axios.create({
  baseURL: '/',
  timeout: 10000
})

const ensureNoLoading = () => {
  return new Promise((resolve) => {
    let e = document.querySelector('[von-loading]')
    if (e) {
      setTimeout(() => {
        $loading.hide()
        setTimeout(() => { resolve() }, 600)
      }, 300)
      return
    }
    resolve()
  })
}

api.interceptors.request
  .use((config) => {
    const re = /^\/s4k\/(.+)$/
    let result = re.exec(config.url)

    if (result) {
      config.url = __ENV__.qk_endpoint + config.url
      _.extend(config.headers, {
        'X-QK-DIS': cookie.get('DIS4')
      })
    } else {
      config.url = __ENV__.ws_endpoint + config.url
      config.withCredentials = true
    }

    !config.headers['Content-Type']
      ? config.headers['Content-Type'] = 'application/x-www-form-urlencoded'
      : void 666

    if (config.method.toLowerCase() === 'post' && config.data &&
      config.headers['Content-Type'] === 'application/x-www-form-urlencoded') {
      config.data = qs.stringify(config.data)
    }

    return config
  }, (err) => { // eslint-disable-line handle-callback-err
    // TODO:
    // Just swallow the err
    return Promise.reject('')
  })

api.interceptors.response
  .use((res) => {
    // 
    let messages = res.data && res.data.messages

    //  messages ,  message,
    // ,
    // 1, 

    // js patch 
    let jpMsg = _.find(messages, function (m) {
      return m.type == 104
    })

    // 
    // 101/102 100/103 
    let keyMsg = _.find(messages, function (m) {
      return m.type == 101 || m.type == 102 || m.type == 103 || m.type == 100
    })

    // 
    let errMsg = _.find(messages, function (m) {
      return m.type == 2
    })

    // 
    let special = _.find(messages, function (m) {
      return _.indexOf([
        3,   // 
        4,   // 
        6,   // 
        7,   // 
        9,   // 
        10,  // 
        12,  // 
        13,  // 
        14,  // 
        15,  // 
        16,  // 
        19,  // 
        20,  // 
        203, // 
        204, // 
        509, // 
        510, // 
        511, // 
        512, // 
        700  // 
      ], m.type) > -1
    })

    // 
    if (jpMsg) { // ,kill
      $assistant.kill()
      return
    }

    // 
    if (errMsg) {
      commonErr(errMsg.content)
    }

    if (keyMsg) {
      if (keyMsg.type == 100 || keyMsg.type == 103) {
        $assistant.makePopup('invalid', keyMsg).then((popup) => {
          popup.show().then((buttonIndex) => {
            if (buttonIndex == 0) {
              location.href = keyMsg.download_url
            }
          })
        })
      } else if (keyMsg.type == 101 || keyMsg.type == 102) {
        $assistant.makePopup('upgrade', keyMsg).then((popup) => {
          popup.show().then((buttonIndex) => {
            if (buttonIndex == 0) {
              location.href = keyMsg.download_url
            }
            if (buttonIndex == 1) {
              // 
              $api.assistantInfo().then(payload => {
                let scheme = payload.app_scheme
                if (scheme) location.href = scheme + 'openURL?type=outside&url=' + encodeURIComponent(location.href)
              })
            }
          })
        })
      }
    }

    if (special) {
      // 
      if (special.type == 700) {
        openRebateEntrance(special.id)
      }
      // 
      if ([3, 4, 6, 7].indexOf(special.type) > -1) {
        taskReward(special.reward, special.content, '', special.id)
      } else if (special.type == 10) {
        // 
        eggTaskAlert(special.content, special.id)
      }
      // 
      if (special.type == 12) {
        creditReward(special.reward, special.content, special.id)
      }
      // 
      if (special.type == 14) {
        gameReward(special.reward, special.title, special.id)
      }
      // 
      if (special.type == 9) {
        highearnReward(special.reward, special.content, special.id)
      }
      // 
      if (special.type == 509) {
        highearnFail(special.content, special.id)
      }
      if (special.type == 19 || special.type == 15 || special.type == 203 || special.type == 511) {
        taskAppealSuccess(special.content, special.id, special.type)
      }
      // 
      if (special.type == 510 || special.type == 16 || special.type == 204 || special.type == 512) {
        taskAppealFail(special.content, special.id, special.type)
      }
      // 
      if (special.type == 13) {
        counterReward(special.chip_num, special.obtain_type, special.jackpot)
      }
      // 
      if (special.type == 20) {
        nationalDay()
      }
    }

    return res
  }, (err) => {
    // Process error:
    let res = err.response
    let config = err.config
    if (config.no_error_handler) return Promise.reject(err)

    if (res) {
      if (res.status == 400) {
        ensureNoLoading().then(() => $toast.show(res.data && res.data.err_msg || ''))
      } else if (res.status == 401) { // 
        const duration = 2000

        $loading.show('..', duration)
        setTimeout(() => {
          location.href = '/v4'
        }, duration + 500)
      } else if (res.status == 402) {
        switch (res.data && res.data.err_code) {
          case 40201:
            // 
            location.href = '/v4/human'
            break
          case 40202:
            ensureNoLoading().then(() => {
              channel.$emit(events.POPUP_BIND_ZHIMA)
            })
        }
      } else if (res.status == 403) {
        // no magic here
      } else if (res.status == 500) {
        ensureNoLoading().then(() => $toast.show(''))
      } else if (res.status == 502) {
        ensureNoLoading().then(() => $toast.show(''))
      } else {
        ensureNoLoading().then(() => $toast.show(''))
        throw err
      }
    } else {
      ensureNoLoading().then(() => $toast.show(''))
    }

    return Promise.reject(err)
  })

const _get = api.get
const _post = api.post
const keep_cache_default = 5000

api.get = (url, config) => {
  console.log(config)
  let keep_cache = keep_cache_default
  if (config && config.keep_cache != undefined) {
    keep_cache = config.keep_cache
  }

  let key = 'api:' + url
  let cache = $storage.get(key)
  let now = (new Date()).getTime()

  return new Promise((resolve, reject) => {
    console.info(key + ' => ', (cache && (now - cache.time < keep_cache)) ? '' : 'api')
    if ((cache && (now - cache.time < keep_cache))) {
      resolve(cache.res)
      return
    }

    _get(url, config).then((res) => {
      cache = {
        res: res,
        time: (new Date()).getTime()
      }
      $storage.set(key, cache)
      resolve(res)
    }, (err) => {
      reject(err)
    })
  })
}

api.post = _post

